(function ($, window, document) {
  function preloadImages() {
    var pic1 = new Image();
    var pic2 = new Image();
    var pic3 = new Image();
    var pic4 = new Image();
    pic1.src = "../../images/portfolio/gruzino/img_1.jpg";
    pic2.src = "../../images/team/img_1.png";
    pic3.src = "../../images/contacts/1.jpg";
    pic4.src = "../../images/awards/diploms/1.jpg";
  }
  $(() => {
    if (!$(".js_page-blog").length || !$(".js_page-project").length) {
      preloadImages();
    }
  });
})(window.jQuery, window, document);
